.header {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: 'rgba(243, 55, 162, 0.29)';
    width: 100%;
    height: 90px;
    z-index: 1000;
}

.right-items {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.btn {
    width: 4rem;
    height: 4rem;
}

.selection {
    color: white;
